.video {
  text-align: center;
  padding: 17px 20px 0;

  @media (min-width: $tablet-width) {
    padding-top: 60px;
  }
}

.video__intro {
  max-width: 445px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    margin-bottom: 60px;
  }
}

.video__player {
  position: relative;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;

  img {
    width: 100%;
    vertical-align: top;
  }
}

.video__play {
  position: absolute;
  inset: 0;
  padding: 0;
  border: none;
  cursor: pointer;
  opacity: 0.7;
  background-color: transparent;
  outline: none;
  width: 100%;

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    margin: auto;
    width: 48px;
    height: 54px;
    background-color: $white;
    clip-path: polygon(100% 50%, 0 0, 0 100%);
  }

  &:hover {
    opacity: 1;
  }

  &:focus-visible {
    opacity: 1;

    &::after {
      content: "";
      position: absolute;
      inset: 0;
      margin: auto;
      width: 70px;
      height: 70px;
      border: 4px solid #ffffff;
      left: -5px;
    }
  }
}
